import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Box, Card, Container, TextareaAutosize, Typography, styled } from '@mui/material'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import { type ClubMemberForAuth } from 'src/models/ClubMember'
import { Semester, dateParams, getCurrentSemester, getCurrentYear } from 'src/utils/date'

import { calcSemester } from '../CalcSemeter'
import PageHeader from '../PageHeader'

interface Questions {
  question1: string
  question2: string
  question3: string
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const Form: React.FC = () => {
  const authUser = useAuthUser()
  const { id } = useParams()
  const clubId = Number(id)
  const [questions, setQuestions] = useState<Questions>({ question1: '', question2: '', question3: '' })
  const [isEditMode, setIsEditMode] = useState<boolean>(false)
  const { enqueueSnackbar } = useSnackbar()
  const club = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId)[0]
  const authHeader = useAuthHeader()
  const [year, setYear] = useState(getCurrentYear())
  const [semester, setSemester] = useState<Semester>(getCurrentSemester())

  const handleSubmit = async () => {
    axios({
      method: isEditMode ? 'put' : 'post',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}/form`,
      data: {
        question1: questions.question1,
        question2: questions.question2,
        question3: questions.question3,
        semester: calcSemester(new Date())
      },
      params: dateParams,
      headers: { Authorization: authHeader() }
    })
      .then(res => {
        if (res.data.message === 'ok') {
          enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        } else {
          throw new Error()
        }
      })
      .catch(() => {
        enqueueSnackbar('등록에 실패했습니다. 다시 시도하여 주십시오.', { variant: 'error' })
      })
  }

  // TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
  useEffect(() => {
    axios.get(`${process.env.REACT_APP_APIHOST}/club/${clubId}/form/`).then((res: AxiosResponse<Questions>) => {
      if (res.data.question1 !== '' || res.data.question2 !== '' || res.data.question3 !== '') {
        setIsEditMode(true)
        setQuestions({ question1: res.data.question1, question2: res.data.question2, question3: res.data.question3 })
      }
    })
  }, [])

  const handleYearSet = (year: number) => {
    setYear(year)
  }

  const handleSemesterSet = (semester: Semester) => {
    setSemester(semester)
  }

  return (
    <>
      <Helmet>
        <title>소개 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <DateForm {...{ clubId, year, handleYearSet, semester, handleSemesterSet }} />
        <Card>
          <Box p={{ xs: 2, md: 5 }}>
            <Typography variant="h2">신청폼 관리</Typography>
            <Typography variant="subtitle1">(커스텀 질문 미사용시 빈칸 처리)</Typography>
            <Box mt={3}>
              <Typography variant="h4">커스텀 질문 1</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question1}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question1: e.target.value }
                  })
                }
              />
              <Typography variant="h4">커스텀 질문 2</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question2}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question2: e.target.value }
                  })
                }
              />
              <Typography variant="h4">커스텀 질문 3</Typography>
              <CustomTextareaAutosize
                maxRows={4}
                minRows={3}
                value={questions.question3}
                onChange={e =>
                  setQuestions(prevState => {
                    return { ...prevState, question3: e.target.value }
                  })
                }
              />
            </Box>
          </Box>
        </Card>
        <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText={isEditMode ? '수정' : '등록'} />
      </Container>
    </>
  )
}

export default Form
